import React from 'react'
import Helmet from 'react-helmet'
import { graphql } from "gatsby"
import Layout from '../components/layout'
import Gallery from '../components/Gallery'
import Posts from '../components/Posts'

const HomeIndex = ({ siteTitle, siteDescription, data }) => (
    <Layout>
        <Helmet>
                <title>{siteTitle}</title>
                <meta name="description" content={siteDescription} />
        </Helmet>
        <div id="main">
            <section id="one">
                <header className="major">
                    <h2>Welcome!</h2>
                </header>
                <p
                    dangerouslySetInnerHTML={{ __html: data.Bio.html }}
                >
                </p>
                <ul className="actions">
                    <li><a href="cv/" className="button">Full CV</a></li>
                    <li><a href="work/" className="button">Some of my projects</a></li>
                </ul>
            </section>
            <section id="two">
            </section>
        </div>
    </Layout>
)

export default HomeIndex

export const query = graphql`
  query {
     Bio: markdownRemark(frontmatter: { type: { eq: "bio" } }) {
        html
		frontmatter {
			title 
			date
		}
	},
    WorkMD: allMarkdownRemark(
        filter: { frontmatter: { type: { eq: "work" } } }
        sort: { order: DESC, fields: [frontmatter___date]},
    ) {
        edges {
            node {
                html
                frontmatter {
                    title
                    date
                    image
                }
                fields {
                    image {
                        childImageSharp {
                            fluid(maxHeight: 500) {
                                ...GatsbyImageSharpFluid
                            }
                        }
                    }
                }
            }
        }
    }
}
`;
